.todo-item-wrapper {
    padding: 20px;
}


/* text strikethrough or not based on completed */
.completed-class {
    text-decoration: line-through;
}

.not-completed-class {
    text-decoration: none;
}

.delete-button-class {
    display: inline;
    padding: 0px;
    text-align: center;
    width: 60px;
    height: 20px;
    background-color: white;
    border-radius: 5px;
    border-color: white;
    font-family: "Courier";
}

.complete-checkbox-class {
    display: inline;
}

p {
    display: inline;
    padding-left: 20px;
    padding-right: 20px;
}
div {
    font-family: "Courier";
    text-align: center;
}

#add-input {
    border-radius: 10px;
}

#add-input:focus {
    border-color: salmon;
    background-color: whitesmoke;
    outline: none;
}

#add-button {
    background-color: salmon;
    border-radius: 10px;
    border-width: 1px;
    border-color: white;
    padding: 3px;
    padding-left: 10px;
    padding-right: 10px;
    margin-left: 5px;
    font-family: "Courier";
}

.form-wrapper {
    margin-top: 30px;
    margin-bottom: 60px;
}


